.content{
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.messages, .people{
  min-height: 500px;
}

.messages{
  width: 70%;
  background: #cfe8ff;
  padding: 2.5%;
  position: relative;
}
.people {
  padding: 20px;
  width: 30%;
  background: #f4f5ff;
}
.message{
  background: lightskyblue;
  border-radius: 10px;
  padding: 10px;
  margin: 5px 0;
}

.person{
  display: block;
  padding: 10px 0px;
  color: #000;
  text-decoration: none;
}
.personSelected{
  font-weight: 700;
}
.input div{
  height: 50px;
  resize: none;
  /*bottom: 20px;*/
  /*position: absolute;*/
  width: 72.5%;
  padding: 5px;
}
.send {
  height: 50px;
  resize: none;
  /*right: 20px;*/
  /*bottom: 20px;*/
  /*position: absolute;*/
  width: 21%;
  height: 50px;
}
.form{
  width: 90%;
  position: absolute;
  display: flex;
  bottom: 20px;
  right: 20px;
}
.form textarea{
  flex-grow: 1;

}